@use 'base/variables' as v;
@use 'base/mixins' as m;


.galeria{
    padding: 5rem;
    h3{
        text-align: center; 
    }
}


.galeria-imagenes{
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.telefono{
        @include m.grid(3, 2rem);
    }
    @include m.tablet{
        @include m.grid(4, 2rem);
    }
    @include m.desktop{
        @include m.grid(6, 2rem);
    }
}
.overlay{
    position: fixed;    //posiciona como fijo el elemento overlay para que se vea encima de la pagina
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; 
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center; //place-items pertenece a grid
    //background-color: rgba(0,0,0,0.5);
    background-color: rgb(0 0 0 / .5); //agrega transparencia al background
    picture{
        display: block; //para que le afecte el max-width
        max-width: 80rem; //fij 80rem a la imagen sobrepuesta
    }
}

.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display:grid; //activa grid para usar place-items
    place-items: center;
    font-weight: 700;
    &:hover{    //.btn-cerrar:hover. & quita el espacio generado por sass
        cursor: pointer;
    }
}