@use 'variables' as v; //para poder usar las variables de _variables
html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body{                       //accede a body.fijar-body
        overflow: hidden;   
    }
    &.body-scroll{ //clase para evitar ue se desplace el body al sobreponer el header
        padding-top: 8.6rem; //deja el espacio suficiente y exacto para sobreponer el header
    }
}
p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
}
h1, h2, h3{
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 { 
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}